@import url(//db.onlinewebfonts.com/c/7d5bdb38da748fe15065cfaee19080c5?family=Beaufort);

:root {
  --text-color: black;
  --menu-color: #111111;
  --main-color: #eaeaea;
  --button-color: #de2d2d;
}

.App {
  text-align: center;
  display: inline-flex;
  width: 100%;
  height: 100%;
  font-family: Open Sans,Arial,Georgia,sans-serif;
  color: var(--menu-color);
}

button {
  background-color: var(--button-color);
  border: outset;
  border-color: var(--button-color);
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: white;
}

.menu {
  display: block;
  width: 20%;
  height: 100%;
  padding-top: 2%;
  padding-left: 1%;
  padding-right: 1%;
  background-color: var(--menu-color);
  color: var(--main-color);
  position: fixed;
  text-align: left;
  align-items: center;
}

.menu-title {
  display: flex;
  justify-content: center;
  margin-bottom: 20%;
}

.menu-filter-container {
  padding-bottom: 1%;
}

.menu-button-container {
  display: flex;
  flex-wrap: wrap;
  font-weight: normal;
}

.body {
  padding-left: 22%;
  padding-top: 2%;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  color: black;
}

.card-div {
  display: block;
  padding-bottom: 2%;
  width: 18%;
}

.card-div div {
  display: flex;
  justify-content: center;
}

.card-div img {
  width: 70%;
}

